import React, { useEffect, useState } from 'react';
import { isEmpty,endsWith } from "lodash";
import { Fragment } from 'react/cjs/react.production.min';
const Calculatour = () => {
 const[getNum1,setNum1]=useState('')
 const [getequal,setequal]=useState()
 const[getErorText,setErorText]=useState('')
 
 
 const deleted =()=>{
     setNum1('')
     setErorText('')
 }
 const EditNum=()=>{
let editedNumber;
const numLength=getNum1.length
const change_to_string=String(getNum1)
editedNumber=change_to_string.slice(0,(numLength-1))
setNum1(editedNumber)
 }
const opration =(oprations)=>{
    if(getNum1[getNum1.length-1]===oprations){
return null;
    }else{
    switch(oprations){
        case "+":
            return setNum1(getNum1+'+');
        case "-":
            return setNum1(getNum1+'-');
        case "*":
            return setNum1(getNum1+'*');
        case "/":
            return setNum1(getNum1+'/');
        case "%":
            return setNum1(getNum1+'%');
            default:
                return 0;
    }
}
}
const equalFunc =()=>{
if(getNum1.includes('/*') || getNum1.includes('*/') || getNum1.includes('/%') || getNum1.includes('%/') || getNum1.includes('*%') || getNum1.includes('%*')){
    setErorText(' You have a error pleas clear and try again')
}else if(isEmpty(getNum1)){
     console.log("write");
    }else if( !endsWith(getNum1,'+',getNum1.length) && 
              !endsWith(getNum1,'-',getNum1.length)&&  
              !endsWith(getNum1,'*',getNum1.length)&&  
              !endsWith(getNum1,'/',getNum1.length)&&  
              !endsWith(getNum1,'%',getNum1.length)&&
              !endsWith(getNum1,'%',1)&&
              !endsWith(getNum1,'*',1)&& 
              !endsWith(getNum1,'/',1)
              ){
        const result=eval(getNum1)
        setNum1(String(result))
      console.log(result);
      console.log(typeof result);
    }
}
    return ( 
        <Fragment>
                <div className="form">
                  <div className="grid-container">    
                     <div className="display">
            <div className="inner-display">
                <input type="text" className='inner-text' placeholder='0' value={getNum1} />
                <input type="text"  className={`${isEmpty(getErorText)?'inner-text2':'inner-text2 inner-text-placeHolder'}`} placeholder={`${(isEmpty(getErorText)?"":getErorText)}`}  disabled/>
            </div>
            
        </div>
        <div className="equate">
            <div onClick={equalFunc} className="btn"><div className="inner-button" id="equate">=</div></div>
        </div>
        <div onClick={deleted} className="btn"><div className="inner-button yellow" id="clear">AC</div></div>
        <div  onClick={e=>opration("/")}  className="btn"><div className="inner-button yellow">/</div></div>
        <div  onClick={e=>opration("*")}  className="btn"><div className="inner-button yellow">x</div></div>
        <div onClick={EditNum} className="btn"><div className="inner-button yellow" id="backspace">C</div></div>
        <div onClick={e=>setNum1(getNum1+'7')} className="btn"><div className="inner-button">7</div></div>
        <div onClick={e=>setNum1(getNum1+'8')} className="btn"><div className="inner-button">8</div></div>
        <div onClick={e=>setNum1(getNum1+'9')} className="btn"><div className="inner-button">9</div></div>
        <div  onClick={e=>opration("-")}   className="btn"><div className="inner-button yellow">-</div></div>
        <div onClick={e=>setNum1(getNum1+'4')} className="btn"><div className="inner-button">4</div></div>
        <div onClick={e=>setNum1(getNum1+'5')} className="btn"><div className="inner-button">5</div></div>
        <div onClick={e=>setNum1(getNum1+'6')} className="btn"><div className="inner-button">6</div></div>
        <div onClick={e=>opration("+")} className="btn"><div className="inner-button yellow">+</div></div>
        <div onClick={e=>setNum1(getNum1+'1')} className="btn"><div className="inner-button">1</div></div>
        <div onClick={e=>setNum1(getNum1+'2')} className="btn"><div className="inner-button">2</div></div>
        <div onClick={e=>setNum1(getNum1+'3')} className="btn"><div className="inner-button">3</div></div>
        <div  onClick={e=>opration("%")}   className="btn"><div className="inner-button">%</div></div>
        <div onClick={e=>setNum1(getNum1+'0')} className="btn"><div className="inner-button">0</div></div>
        <div onClick={e=>setNum1(getNum1+'.')}  className="btn"><div className="inner-button">.</div></div>
    </div>
</div>

        </Fragment>
     );
}
 
export default Calculatour;